:root{--color-main: #fab702;--clr-primary-1: hsl(22, 28%, 21%);--clr-primary-2: hsl(22, 28%, 29%);--clr-primary-3: hsl(22, 28%, 37%);--clr-primary-4: hsl(22, 28%, 45%);--clr-primary-5: hsl(22, 31%, 52%);--clr-primary-6: hsl(22, 31%, 60%);--clr-primary-7: hsl(22, 31%, 67%);--clr-primary-8: hsl(20, 31%, 74%);--clr-primary-9: hsl(22, 31%, 81%);--clr-primary-10: hsl(22, 31%, 88%);--clr-grey-1: hsl(209, 61%, 16%);--clr-grey-2: hsl(211, 39%, 23%);--clr-grey-3: hsl(209, 34%, 30%);--clr-grey-4: hsl(209, 28%, 39%);--clr-grey-5: hsl(210, 22%, 49%);--clr-grey-6: hsl(209, 23%, 60%);--clr-grey-7: hsl(211, 27%, 70%);--clr-grey-8: hsl(210, 31%, 80%);--clr-grey-9: hsl(212, 33%, 89%);--clr-grey-10: hsl(210, 36%, 96%);--clr-white: #fff;--clr-red-dark: hsl(360, 67%, 44%);--clr-red-light: hsl(360, 71%, 66%);--clr-green-dark: hsl(125, 67%, 44%);--clr-green-light: hsl(125, 71%, 66%);--clr-black: #222;--transition: all 0.3s linear;--spacing: 0.1rem;--radius: 0.25rem;--light-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);--dark-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);--max-width: 1170px;--fixed-width: 620px;--primaryColor: #af9a7d;--mainWhite: #fff;--offWhite: #f7f7f7;--mainBlack: #222;--mainGrey: #ececec;--darkGrey: #cfcfcf;--mainTransition: all 0.3s linear;--mainSpacing: 3px;--lightShadow: 2px 5px 3px 0px rgba(0, 0, 0, 0.5);--darkShadow: 4px 10px 5px 0px rgba(0, 0, 0, 0.5)}*{margin:0;padding:0;box-sizing:border-box}body{font-family:"Josefin Sans", sans-serif;font-family:"Montserrat", sans-serif;font-size:14px;line-height:1.5;background-color:#fff}ul{list-style-type:none}a{text-decoration:none}hr{border:none;border-top:1px solid hsl(210, 31%, 80%);border-top:1px solid var(--clr-grey-8)}h1,h2,h3,h4,h5{letter-spacing:0.1rem;letter-spacing:var(--spacing);text-transform:capitalize;line-height:1.25;margin-bottom:0.75rem}h1{font-size:2.5rem}h2{font-size:2rem}h3{font-size:1.5rem}h4{font-size:1.25rem}h5{font-size:0.875rem}p{margin-bottom:1.25rem;color:hsl(209, 34%, 30%);color:var(--clr-grey-3)}@media screen