.navbar{height:5rem;display:flex;align-items:center;background:transparent;justify-content:center;background:rgba(0,0,0,0.5);position:absolute;top:0;left:0;right:0;z-index:10}.navbar .nav-center{width:90vw;margin:0 auto;max-width:var(--max-width)}.navbar .nav-header{display:flex;align-items:center;justify-content:space-between}.navbar .nav-header img{width:175px;margin-left:-15px}.navbar .nav-toggle{background:#606060;height:38px;width:38px;line-height:22px;font-size:22px;color:#fff;transition:all 0.3s ease;cursor:pointer;border:none}.navbar .nav-toggle:hover{color:#111;background:#fff}.navbar .nav-toggle:focus{outline:none}.navbar .nav-links{display:none}.navbar .cart-btn-wrapper{display:none}@media (min-width: 992px){.navbar .nav-toggle{display:none}.navbar .nav-center{display:grid;grid-template-columns:auto 1fr auto;align-items:center}.navbar .nav-links{display:flex;justify-content:center;align-items:center}.navbar .nav-links li{margin:1rem 0.5rem 0}.navbar .nav-links a{color:#fff;font-size:1rem;text-transform:capitalize;letter-spacing:var(--spacing);padding:0.5rem;font-size:12px;text-transform:uppercase}.navbar .nav-links a:hover{border-bottom:2px solid var(--color-main);color:var(--color-main)}.navbar .cart-btn-wrapper{display:grid;grid-template-columns:1fr 1fr;align-items:center;width:160px;color:#fff;justify-content:center}.navbar .cart-btn-wrapper .account{display:flex;justify-content:center;align-items:center}.navbar .cart-btn-wrapper .account .img-avatar{width:30px;height:30px;border-radius:50%;object-fit:cover;margin-left:50px}.navbar .cart-btn-wrapper .cart-btn{color:#fff;font-size:1.5rem;letter-spacing:var(--spacing);display:flex;justify-self:center;align-items:center}.navbar .cart-btn-wrapper .cart-container{display:flex;align-items:center;position:relative}.navbar .cart-btn-wrapper .cart-container svg{height:1.6rem;margin-left:5px}.navbar .cart-btn-wrapper .cart-value{position:absolute;top:-10px;right:-16px;background:var(--color-main);width:16px;height:16px;display:flex;align-items:center;justify-content:center;border-radius:50%;font-size:0.75rem;color:var(--clr-white);padding:12px}.navbar .cart-btn-wrapper .auth-btn{display:flex;align-items:center;justify-content:center;background:transparent;border-color:transparent;font-size:1.5rem;cursor:pointer;color:#fff;letter-spacing:var(--spacing)}.navbar .cart-btn-wrapper .auth-btn svg{margin-left:5px}.navbar .cart-btn-wrapper{display:grid}}.navbar-sticky{position:fixed;top:0;left:0;width:100%;padding:0.75rem 2rem;background:#111;z-index:999;height:4.5rem;transition:all 0.4s ease-in-out;border-bottom:solid 1px #333}.navbar-sticky .nav-links li{margin:0.75rem 0.5rem 0}li .selected{color:red !important}

.sidebar-container{text-align:center;position:relative;z-index:1000}.sidebar-container .sidebar-header{display:flex;justify-content:space-between;align-items:center;padding:1rem 2rem}.sidebar-container .close-btn{background:#606060;height:38px;width:38px;line-height:22px;font-size:22px;color:#fff;transition:all 0.3s ease;cursor:pointer;border:none}.sidebar-container .close-btn:hover{color:#111;background:#fff}.sidebar-container .close-btn:focus{outline:none}.sidebar-container .logo{justify-self:center;height:45px}.sidebar-container .links{margin-bottom:2rem;padding:1rem 2rem}.sidebar-container .links li a{display:block;padding:10px 0 10px 20px;line-height:35px;text-decoration:none;color:#eceff3;text-align:left;outline:none;letter-spacing:1px;text-transform:uppercase;border-bottom:1px solid #333;font-size:13px}.sidebar-container .links li a:hover{color:var(--color-main)}.sidebar-container .links li:last-child a{border:none}.sidebar-container .sidebar{position:fixed;top:0;left:0;width:100%;height:auto;background:#111;transition:var(--transition);-webkit-transform:translate(-100%);transform:translate(-100%);z-index:10}.sidebar-container .show-sidebar{-webkit-transform:translate(0);transform:translate(0);z-index:999}.sidebar-container .cart-btn-wrapper{margin:2rem auto}.sidebar-container .cart-btn-wrapper{display:grid;grid-template-columns:1fr 1fr;align-items:center;width:160px;justify-content:center}.sidebar-container .cart-btn-wrapper .account{display:flex;justify-content:center;align-items:center}.sidebar-container .cart-btn-wrapper .account .img-avatar{width:30px;height:30px;border-radius:50%;object-fit:cover}.sidebar-container .cart-btn-wrapper .cart-btn{font-size:1.5rem;letter-spacing:var(--spacing);color:#eceff3;display:flex;justify-self:center;align-items:center}.sidebar-container .cart-btn-wrapper .cart-container{display:flex;align-items:center;position:relative}.sidebar-container .cart-btn-wrapper .cart-container svg{height:1.6rem;margin-left:5px}.sidebar-container .cart-btn-wrapper .cart-value{position:absolute;top:-10px;right:-16px;background:var(--color-main);width:16px;height:16px;display:flex;align-items:center;justify-content:center;border-radius:50%;font-size:0.75rem;color:#eceff3;padding:12px}.sidebar-container .cart-btn-wrapper .auth-btn{display:flex;align-items:center;justify-content:center;background:transparent;border-color:transparent;font-size:1.5rem;cursor:pointer;color:#eceff3;letter-spacing:var(--spacing)}.sidebar-container .cart-btn-wrapper .auth-btn svg{margin-left:5px}@media screen and (min-width: 992px){.sidebar-container .sidebar{display:none}}

.footer__container{display:flex;justify-content:center;background:#111;height:3rem;line-height:3rem;align-items:center;position:relative;z-index:10}.footer__container h4{font-size:15px;letter-spacing:1px;margin:0;color:#ddd}.footer__container h4 span{color:var(--color-main)}

.backtotop-btn{width:36px;height:36px;line-height:20px;background:var(--color-main) none repeat scroll 0 0;text-align:center;position:fixed;right:20px;border:3px double #111;bottom:20px;z-index:100;cursor:pointer;color:#111;font-size:22px;-webkit-transform:rotate(0deg);transform:rotate(0deg);transition:all 0.5s ease-in-out}.backtotop-btn svg{-webkit-transform:rotate(0deg);transform:rotate(0deg)}.backtotop-btn:hover{background:#111;color:#fff;-webkit-transform:rotate(45deg);transform:rotate(45deg);border-color:#fff}.backtotop-btn:hover svg{-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}

